.menu__link {
	font-size: 16px;
	font-weight: 600;
	color: #fff;
	text-decoration: none;
	text-transform: uppercase; }

.menu__link_active {
	color: #43ABF0; }

.menu-shadow {
	display: none; }

@media only screen and (max-width: 767px) {
	.js-toggle-menu {
		position: relative;
		display: flex;
		width: 20px;
		height: 20px;
		align-items: center;
		justify-content: center;
		z-index: 12;
		transition: color .3s ease; }
	.toggle-menu {
		display: block;
		position: relative;
		width: 20px;
		height: 14px;
		cursor: pointer;
		.toggle-menu_center, &:before, &:after {
			display: block;
			position: absolute;
			width: 20px;
			height: 2px;
			left: 0;
			background-color: #fff;
			border: none;
			transition: transform 0.5s, background-color 0.2s; }
		&:before, &:after {
			content: ''; }
		&:before {
			top: 0; }
		&:after {
			top: 12px; }
		.toggle-menu_center {
			top: 6px; } }
	.js-toggle-menu_active {
		color: #fff;
		.toggle-menu {
			&:before, &:after {
				background-color: #fff;
				top: 6px; }
			&:before {
				-webkit-transform: rotate(-45deg);
				-ms-transform: rotate(-45deg);
				transform: rotate(-45deg); }
			&:after {
				-webkit-transform: rotate(45deg);
				-ms-transform: rotate(45deg);
				transform: rotate(45deg); } }
		.toggle-menu_center {
			background-color: #fff;
			opacity: 0;
			bottom: 0;
			-webkit-transform: rotate(180deg);
			-ms-transform: rotate(180deg);
			transform: rotate(180deg); } }
	.menu-shadow {
		position: fixed;
		width: 100%;
		height: 100%;
		left: 0;
		top: 0;
		background-color: rgba(0, 0, 0, 0.4);
		z-index: 10; }
	.menu-shadow_active {
		display: block; }
	.menu {
		display: flex;
		flex-direction: column;
		position: fixed;
		padding: 60px 15px;
		width: 100%;
		max-width: 320px;
		height: 100%;
		right: -100%;
		top: 0;
		overflow-y: auto;
		transition: right .4s ease;
		z-index: 11;
		background-color: #10101D;
		text-align: center;
		.btn {
			width: 100%;
			background-color: $base;
			border-color: $base;
			color: #fff; } }
	.menu_open {
		right: 0; }
	.menu__body {
		position: relative;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		overflow-y: auto;
		z-index: 1; }
	.menu__item {
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0; } } }

@media only screen and (min-width: 768px) {
	.menu__body {
		display: flex;
		flex-wrap: wrap;
		align-items: center; }
	.menu__item {
		margin-left: 30px; }
	.menu__link {
		font-size: 13px; }
	.js-toggle-menu {
		display: none; } }

@media only screen and (min-width: 1200px) {
	.menu__item {
		margin-left: 60px; } }

@media (hover), (min-width: 0\0), (min--moz-device-pixel-ratio: 0) {
	.menu__link {
		transition: color .3s ease;
		&:hover {
			color: $blue; } } }
