.btn {
	display: inline-block;
	padding: 14px 30px;
	border: 1px solid #fff;
	border-radius: 4px;
	background-color: $blue;
	color: #fff;
	font-size: 13px;
	text-transform: uppercase;
	text-align: center;
	text-decoration: none; }

.btn_white {
	border-color: #6F6D7B;
	color: #2A2B30;
	background-color: #fff; }

.form__field-wrapper {
	margin-bottom: 15px; }

.form__field {
	height: 48px;
	width: 100%;
	padding: 0 9px;
	background: rgba(255, 255, 255, 0.85);
	border: 1px solid #fff;
	border-radius: 3px;
	@include placeholder(#272733);
	font-size: 18px;
	&:focus {
		border-color: $blue;
		background-color: #fff; } }

.form__textarea {
	width: 100%;
	border: 1px solid $base;
	height: 120px;
	resize: none;
	padding: 10px;
	font-size: 18px;
	@include placeholder($base); }

.form__label {
	display: flex;
	justify-content: space-between;
	position: relative;
	margin-bottom: 10px;
	color: $base;
	sup {
		@include posRightCenter;
		width: 15px;
		height: 15px;
		background-color: $red;
		flex-shrink: 0;
		.icon {
			@include posCenter;
			width: 9px;
			height: 9px; } } }

.form__file-wrapper {
	width: 100%;
	height: 48px;
	padding: 14px 9px;
	position: relative;
	text-align: center;
	background: rgba(255, 255, 255, 0.85);
	border: 1px solid $blue;
	border-radius: 3px;
	font-size: 15px;
	font-weight: 600;
	cursor: pointer;
	color: #272733;
	text-transform: uppercase; }

.form__file-path-wrapper {
	display: inline-block;
	max-width: 100%;
	padding-left: 28px;
	position: relative;
	.icon {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		width: 18px;
		height: 21px;
		fill: #272733; } }

.form__file-path {
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap; }

.form__file-info {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0; }

.form__input-file {
	display: none; }

.form__radio {
	position: absolute;
	left: 4px;
	opacity: 0;
	visibility: hidden;
	&:checked + .form__radio-label {
		&:after {
			content: '';
			position: absolute;
			left: 4px;
			top: 4px;
			width: 12px;
			height: 12px;
			background-color: $base; } } }

.form__radio-wrapper {
	position: relative;
	margin-bottom: 20px; }

.form__radio-label {
	position: relative;
	padding-left: 31px;
	cursor: pointer;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 20px;
		height: 20px;
		background-color: #fff;
		border: 1px solid $base; } }

.form__checkbox-wrapper {
	@extend .form__radio-wrapper; }

.form__checkbox {
	position: absolute;
	left: 4px;
	opacity: 0;
	visibility: hidden;
	&:checked + .form__checkbox-label {
		&:after {
			content: '';
			position: absolute;
			width: 13px;
			height: 11px;
			left: 3px;
			top: 4px;
			background: center / cover no-repeat url('../images/tick.svg'); } } }

.form__checkbox-label {
	position: relative;
	padding-left: 31px;
	cursor: pointer;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		width: 20px;
		height: 20px;
		background-color: #fff;
		border: 1px solid $base; } }

.err_mess {
	margin-top: 10px;
	color: $red; }

.complete {
	margin-top: 10px;
	color: $green; }

.err {
	border-color: $red; }

@media (hover), (min-width: 0\0), (min--moz-device-pixel-ratio: 0) {
	.btn {
		transition: color .3s ease, background-color .3s ease;
		&:hover {
			background-color: #fff;
			color: $blue; } }
	.btn_white {
		&:hover {
			background-color: $blue;
			color: #fff; } }
	.form__file-wrapper {
		transition: color .3s ease, background-color .3s ease;
		.icon {
			transition: fill .3s ease; }
		&:hover {
			background-color: $blue;
			color: #fff;
			.icon {
				fill: #fff; } } } }
