@charset "UTF-8";
@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Black.woff") format("woff"), url("../fonts/Lato-Black.woff2") format("woff2");
  font-weight: 900;
  font-style: "normal";
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Heavy.woff") format("woff"), url("../fonts/Lato-Heavy.woff2") format("woff2");
  font-weight: 800;
  font-style: "normal";
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Bold.woff") format("woff"), url("../fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: "normal";
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Semibold.woff") format("woff"), url("../fonts/Lato-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-style: "normal";
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Italic.woff") format("woff"), url("../fonts/Lato-Italic.woff2") format("woff2");
  font-weight: 400;
  font-style: "italic";
}

@font-face {
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/Lato-Regular.woff") format("woff"), url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: "normal";
}

html, body, div, span, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, address, em, img, q, samp, small, strong, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, nav, section, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

ol, ul {
  list-style: none;
}

img {
  vertical-align: top;
  max-width: 100%;
  height: auto;
}

input[type="text"]:focus {
  outline: none;
}

button, input, textarea {
  line-height: inherit;
  outline: none;
}

button, input, select, textarea {
  font-size: 100%;
  margin: 0;
  vertical-align: baseline;
  font-family: "Lato", sans-serif, Arial, Helvetica, Verdana;
  font-weight: 400;
  *vertical-align: middle;
}

input, button, textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  border-radius: 0;
}

button, input[type="submit"], input[type="reset"], input[type="button"] {
  cursor: pointer;
}

b, strong {
  font-weight: 700;
}

a {
  text-decoration: underline;
}

a:hover {
  text-decoration: none;
}

em, i {
  font-style: italic;
}

input[disabled] {
  cursor: default;
  background-color: #f5f5f5;
}

input[type=checkbox], input[type=radio] {
  *height: 13px;
  padding: 0;
  *width: 13px;
}

input[type=search]::-webkit-search-decoration, input[type=search]::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

html * {
  box-sizing: border-box;
  text-size-adjust: 100%;
  font-size: 100%;
  outline: none;
}

html *:before, html *:after {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  background: #fff;
  color: #000;
  direction: ltr;
  font: 400 14px/1.2 "Lato", sans-serif, Arial, Helvetica, Verdana;
}

/*overflow-anchor: none для прокрутки */
/*при использовании scale родительскому блоку добавить стиль backface-visibility: hidden*/
/*и добавить самой картинке will-change: transform*/
.form__select {
  opacity: 0;
  height: 48px;
  width: 200px;
}

.selects span {
  display: block;
  margin-bottom: 10px;
}

.SumoSelect p {
  margin: 0;
}

.SumoSelect {
  width: 100%;
  font-size: 18px;
  cursor: pointer;
}

.sumoStopScroll {
  overflow: hidden;
}

/* Filtering style */
.SumoSelect .hidden {
  display: none;
}

.SumoSelect .search-txt {
  display: none;
  outline: none;
}

.SumoSelect .no-match {
  display: none;
  padding: 6px;
}

.SumoSelect.open .search-txt {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0 20px;
  height: 38px;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 5px;
}

.SelectClass, .SumoUnder {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
  filter: alpha(opacity=0);
  opacity: 0;
}

.SelectClass {
  z-index: 1;
}

.SumoSelect .optWrapper .options li.opt label, .SumoSelect .CaptionCont, .SumoSelect .select-all label {
  user-select: none;
}

.SumoSelect {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  outline: none;
  cursor: pointer;
}

.SumoSelect .CaptionCont {
  position: relative;
  display: block;
  border: 1px solid #fff;
  border-radius: 3px;
  min-height: 48px;
  height: 48px;
  margin: 0;
  background: rgba(255, 255, 255, 0.85);
}

.SumoSelect.open .CaptionCont {
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.SumoSelect .CaptionCont span {
  display: block;
  padding-right: 36px;
  padding-left: 9px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
  line-height: 48px;
  color: #272733;
  font-size: 18px;
  font-family: "Lato", sans-serif, Arial, Helvetica, Verdana;
}

/*placeholder style*/
.SumoSelect .CaptionCont span.placeholder {
  color: #ccc;
  font-style: italic;
}

.SumoSelect .CaptionCont label {
  position: absolute;
  top: 0;
  right: 9px;
  bottom: 0;
  margin: auto 0;
  width: 20px;
  height: 20px;
  pointer-events: none;
}

.SumoSelect .CaptionCont label .icon {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  width: 20px;
  height: 20px;
  fill: #000;
}

.SumoSelect .optWrapper {
  display: none;
  z-index: 4;
  top: 30px;
  width: 100%;
  position: absolute;
  left: 0;
  box-sizing: border-box;
  background-color: #272733;
  border: 1px solid #3D4050;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 3px;
  overflow: auto;
}

.SumoSelect.open .optWrapper {
  top: 100%;
  display: block;
  margin-top: -1px;
  max-height: 166px;
  overflow: hidden;
  padding: 4px 0;
}

.SumoSelect.open .optWrapper.up {
  top: auto;
  bottom: 100%;
  margin-bottom: 5px;
}

.SumoSelect .optWrapper ul {
  list-style: none;
  display: block;
  padding: 0;
  margin: 0;
  overflow: auto;
}

.SumoSelect .optWrapper .options {
  position: relative;
  margin-bottom: 0;
}

.SumoSelect.open .CaptionCont {
  border-color: #3D4050;
  background-color: #3D4050;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}

.SumoSelect.open .CaptionCont span {
  color: #fff;
}

.SumoSelect.open .CaptionCont label .icon {
  transform: rotate(180deg);
  fill: #fff;
}

/*height*/
.SumoSelect .optWrapper.okCancelInMulti .options {
  border-radius: 2px 2px 0 0;
}

.SumoSelect .optWrapper.selall .options {
  border-radius: 0 0 2px 2px;
}

.SumoSelect .optWrapper.selall.okCancelInMulti .options {
  border-radius: 0;
}

.SumoSelect .optWrapper .options li.group.disabled label {
  opacity: 0.5;
}

.SumoSelect .optWrapper .options li ul li.opt {
  padding-left: 22px;
}

.SumoSelect .optWrapper.multiple .options li ul li.opt {
  padding-left: 50px;
}

.SumoSelect .optWrapper.isFloating .options {
  max-height: 100%;
  box-shadow: 0 0 100px #595959;
}

.SumoSelect .optWrapper .options li.opt {
  padding: 5px 9px;
  position: relative;
  color: #fff;
  margin-bottom: 0;
  transition: color .3s ease;
}

.SumoSelect .optWrapper .options li.opt:before {
  display: none;
}

.SumoSelect .optWrapper.selall .options li.opt:first-child {
  border-radius: 0;
}

.SumoSelect .optWrapper .options li.opt:last-child {
  border-bottom: none;
}

.SumoSelect .optWrapper.okCancelInMulti .options li.opt:last-child {
  border-radius: 0;
}

.SumoSelect .optWrapper .options li.opt:hover {
  color: #42A9ED;
}

.SumoSelect .optWrapper .options li.opt.selected {
  color: #42A9ED;
}

.SumoSelect .optWrapper .options li.opt.sel, .SumoSelect .select-all.sel {
  background-color: #a1c0e4;
}

.SumoSelect .optWrapper .options li label {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  display: block;
  cursor: pointer;
}

.SumoSelect .optWrapper .options li span {
  display: none;
}

.SumoSelect .optWrapper .options li.group label {
  cursor: default;
  padding: 8px 6px;
  font-weight: bold;
}

/*Floating styles*/
.SumoSelect .optWrapper.isFloating {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 90%;
  bottom: 0;
  margin: auto;
  max-height: 90%;
}

/*disabled state*/
.SumoSelect .optWrapper .options li.opt.disabled {
  background-color: inherit;
  pointer-events: none;
}

.SumoSelect .optWrapper .options li.opt.disabled * {
  opacity: 0.5;
}

/*styling for multiple select*/
.SumoSelect .optWrapper.multiple .options li.opt {
  padding-left: 35px;
  cursor: pointer;
}

.SumoSelect .optWrapper.multiple .options li.opt span, .SumoSelect .select-all span {
  position: absolute;
  display: block;
  width: 30px;
  top: 0;
  bottom: 0;
  margin-left: -35px;
}

.SumoSelect .optWrapper.multiple .options li.opt span i, .SumoSelect .select-all span i {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 14px;
  height: 14px;
  border: 1px solid #AEAEAE;
  border-radius: 2px;
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.15);
  background-color: #fff;
}

.SumoSelect .optWrapper .MultiControls {
  display: none;
  border-top: 1px solid #ddd;
  background-color: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.13);
  border-radius: 0 0 3px 3px;
}

.SumoSelect .optWrapper.multiple.isFloating .MultiControls {
  display: block;
  margin-top: 5px;
  position: absolute;
  bottom: 0;
  width: 100%;
}

.SumoSelect .optWrapper.multiple.okCancelInMulti .MultiControls {
  display: block;
}

.SumoSelect .optWrapper.multiple.okCancelInMulti .MultiControls p {
  padding: 6px;
}

.SumoSelect .optWrapper.multiple.okCancelInMulti .MultiControls p:focus {
  box-shadow: 0 0 2px #a1c0e4;
  border-color: #a1c0e4;
  outline: none;
  background-color: #a1c0e4;
}

.SumoSelect .optWrapper.multiple .MultiControls p {
  display: inline-block;
  cursor: pointer;
  padding: 12px;
  width: 50%;
  box-sizing: border-box;
  text-align: center;
}

.SumoSelect .optWrapper.multiple .MultiControls p:hover {
  background-color: #f1f1f1;
}

.SumoSelect .optWrapper.multiple .MultiControls p.btnOk {
  border-right: 1px solid #DBDBDB;
  border-radius: 0 0 0 3px;
}

.SumoSelect .optWrapper.multiple .MultiControls p.btnCancel {
  border-radius: 0 0 3px 0;
}

/*styling for select on popup mode*/
.SumoSelect .optWrapper.isFloating .options li.opt {
  padding: 12px 6px;
}

/*styling for only multiple select on popup mode*/
.SumoSelect .optWrapper.multiple.isFloating .options li.opt {
  padding-left: 35px;
}

.SumoSelect .optWrapper.multiple.isFloating {
  padding-bottom: 43px;
}

.SumoSelect .optWrapper.multiple .options li.opt.selected span i, .SumoSelect .select-all.selected span i, .SumoSelect .select-all.partial span i {
  background-color: #11a911;
  box-shadow: none;
  border-color: transparent;
  background-image: url("data:image/pngbase64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAAGCAYAAAD+Bd/7AAAABHNCSVQICAgIfAhkiAAAAAlwSFlzAAALEgAACxIB0t1+/AAAABx0RVh0U29mdHdhcmUAQWRvYmUgRmlyZXdvcmtzIENTNXG14zYAAABMSURBVAiZfc0xDkAAFIPhd2Kr1WRjcAExuIgzGUTIZ/AkImjSofnbNBAfHvzAHjOKNzhiQ42IDFXCDivaaxAJd0xYshT3QqBxqnxeHvhunpu23xnmAAAAAElFTkSuQmCC");
  background-repeat: no-repeat;
  background-position: center center;
}

/*disabled state*/
.SumoSelect.disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.SumoSelect.disabled .CaptionCont {
  border-color: #ccc;
  box-shadow: none;
}

/**Select all button**/
.SumoSelect .select-all {
  border-radius: 3px 3px 0 0;
  position: relative;
  border-bottom: 1px solid #ddd;
  background-color: #fff;
  padding: 10px 0 10px 35px;
  cursor: pointer;
}

.SumoSelect .select-all label, .SumoSelect .select-all span i {
  cursor: pointer;
}

.SumoSelect .select-all.partial span i {
  background-color: #ccc;
}

/*styling for optgroups*/
.SumoSelect .optWrapper .options li.optGroup {
  padding-left: 5px;
  text-decoration: underline;
}

.SumoSelect {
  text-align: left;
}

/*malihu jquery custom scrollbar plugin ==Plugin URI: http://manos.malihu.gr/jquery-custom-content-scroller*/
.mCustomScrollbar {
  touch-action: pinch-zoom;
}

.mCustomScrollbar.mCS_no_scrollbar, .mCustomScrollbar.mCS_touch_action {
  touch-action: auto;
}

.mCustomScrollBox {
  position: relative;
  overflow: hidden;
  height: 100%;
  max-width: 100%;
  outline: none;
  direction: ltr;
}

.mCSB_container {
  overflow: hidden;
  width: auto;
  height: auto;
}

/*2. VERTICAL SCROLLBAR*/
.mCSB_inside > .mCSB_container {
  margin-right: 11px;
}

.mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden {
  margin-right: 0;
}

/* non-visible scrollbar */
.mCS-dir-rtl > .mCSB_inside > .mCSB_container {
  /* RTL direction/left-side scrollbar */
  margin-right: 0;
  margin-left: 30px;
}

.mCS-dir-rtl > .mCSB_inside > .mCSB_container.mCS_no_scrollbar_y.mCS_y_hidden {
  margin-left: 0;
}

/* RTL direction/left-side scrollbar */
.mCSB_scrollTools {
  /* contains scrollbar markup (draggable element, dragger rail, buttons etc.) */
  position: absolute;
  width: 7px;
  height: auto;
  left: auto;
  top: 0;
  right: 4px;
  bottom: 0;
}

.mCSB_outside + .mCSB_scrollTools {
  right: -26px;
}

/* scrollbar position: outside */
.mCS-dir-rtl > .mCSB_inside > .mCSB_scrollTools,
.mCS-dir-rtl > .mCSB_outside + .mCSB_scrollTools {
  /* RTL direction/left-side scrollbar */
  right: auto;
  left: 0;
}

.mCS-dir-rtl > .mCSB_outside + .mCSB_scrollTools {
  left: -26px;
}

/* RTL direction/left-side scrollbar (scrollbar position: outside) */
.mCSB_scrollTools .mCSB_draggerContainer {
  /* contains the draggable element and dragger rail markup */
  position: absolute;
  top: 2px;
  left: 0;
  bottom: 2px;
  right: 0;
  height: auto;
}

.mCSB_scrollTools a + .mCSB_draggerContainer {
  margin: 20px 0;
}

.mCSB_scrollTools .mCSB_draggerRail {
  width: 7px;
  height: 100%;
  margin: 0 auto;
  border-radius: 16px;
}

.mCSB_scrollTools .mCSB_dragger {
  /* the draggable element */
  cursor: pointer;
  width: 100%;
  height: 30px;
  /* minimum dragger height */
  z-index: 1;
}

.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
  /* the dragger element */
  position: relative;
  width: 7px;
  height: 100%;
  margin: 0 auto;
  border-radius: 16px;
  text-align: center;
}

.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand .mCSB_dragger.mCSB_dragger_onDrag_expanded .mCSB_dragger_bar,
.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand .mCSB_draggerContainer:hover .mCSB_dragger .mCSB_dragger_bar {
  width: 12px;
}

/* auto-expanded scrollbar */
.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand .mCSB_dragger.mCSB_dragger_onDrag_expanded + .mCSB_draggerRail,
.mCSB_scrollTools_vertical.mCSB_scrollTools_onDrag_expand .mCSB_draggerContainer:hover .mCSB_draggerRail {
  width: 8px;
}

/* auto-expanded scrollbar */
.mCSB_scrollTools .mCSB_buttonUp,
.mCSB_scrollTools .mCSB_buttonDown {
  display: block;
  position: absolute;
  height: 20px;
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
  cursor: pointer;
}

.mCSB_scrollTools .mCSB_buttonDown {
  bottom: 0;
}

/*3. HORIZONTAL SCROLLBAR*/
.mCSB_horizontal.mCSB_inside > .mCSB_container {
  margin-right: 0;
  margin-bottom: 30px;
}

.mCSB_horizontal.mCSB_outside > .mCSB_container {
  min-height: 100%;
}

.mCSB_horizontal > .mCSB_container.mCS_no_scrollbar_x.mCS_x_hidden {
  margin-bottom: 0;
}

/* non-visible scrollbar */
.mCSB_scrollTools.mCSB_scrollTools_horizontal {
  width: auto;
  height: 16px;
  top: auto;
  right: 0;
  bottom: 0;
  left: 0;
}

.mCustomScrollBox + .mCSB_scrollTools.mCSB_scrollTools_horizontal,
.mCustomScrollBox + .mCSB_scrollTools + .mCSB_scrollTools.mCSB_scrollTools_horizontal {
  bottom: -26px;
}

/* scrollbar position: outside */
.mCSB_scrollTools.mCSB_scrollTools_horizontal a + .mCSB_draggerContainer {
  margin: 0 20px;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_draggerRail {
  width: 100%;
  height: 2px;
  margin: 7px 0;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_dragger {
  width: 30px;
  height: 100%;
  left: 0;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_dragger .mCSB_dragger_bar {
  width: 100%;
  height: 4px;
  margin: 6px auto;
}

.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand .mCSB_dragger.mCSB_dragger_onDrag_expanded .mCSB_dragger_bar,
.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand .mCSB_draggerContainer:hover .mCSB_dragger .mCSB_dragger_bar {
  height: 12px;
  /* auto-expanded scrollbar */
  margin: 2px auto;
}

.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand .mCSB_dragger.mCSB_dragger_onDrag_expanded + .mCSB_draggerRail,
.mCSB_scrollTools_horizontal.mCSB_scrollTools_onDrag_expand .mCSB_draggerContainer:hover .mCSB_draggerRail {
  height: 8px;
  /* auto-expanded scrollbar */
  margin: 4px 0;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonLeft,
.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonRight {
  display: block;
  position: absolute;
  width: 20px;
  height: 100%;
  overflow: hidden;
  margin: 0 auto;
  cursor: pointer;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonLeft {
  left: 0;
}

.mCSB_scrollTools.mCSB_scrollTools_horizontal .mCSB_buttonRight {
  right: 0;
}

/*--4 . VERTICAL AND HORIZONTAL SCROLLBARS yx-axis*/
.mCSB_container_wrapper {
  position: absolute;
  height: auto;
  width: auto;
  overflow: hidden;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin-right: 30px;
  margin-bottom: 30px;
}

.mCSB_container_wrapper > .mCSB_container {
  padding-right: 30px;
  padding-bottom: 30px;
  box-sizing: border-box;
}

.mCSB_vertical_horizontal > .mCSB_scrollTools.mCSB_scrollTools_vertical {
  bottom: 20px;
}

.mCSB_vertical_horizontal > .mCSB_scrollTools.mCSB_scrollTools_horizontal {
  right: 20px;
}

/* non-visible horizontal scrollbar */
.mCSB_container_wrapper.mCS_no_scrollbar_x.mCS_x_hidden + .mCSB_scrollTools.mCSB_scrollTools_vertical {
  bottom: 0;
}

.mCSB_container_wrapper.mCS_no_scrollbar_y.mCS_y_hidden > .mCSB_container {
  padding-right: 0;
}

.mCSB_container_wrapper.mCS_no_scrollbar_x.mCS_x_hidden > .mCSB_container {
  padding-bottom: 0;
}

.mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > .mCSB_container_wrapper.mCS_no_scrollbar_y.mCS_y_hidden {
  margin-right: 0;
  /* non-visible scrollbar */
  margin-left: 0;
}

/* non-visible horizontal scrollbar */
.mCustomScrollBox.mCSB_vertical_horizontal.mCSB_inside > .mCSB_container_wrapper.mCS_no_scrollbar_x.mCS_x_hidden {
  margin-bottom: 0;
}

/*5 . TRANSITIONS*/
