$font: 'Lato';
$red: #ec1c24;
$green: #2ecc71;
$blue: #3E9CDC;
$base: #000;
$border: #6F6D7B;

@mixin posCenter() {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin posLeftCenter() {
	position: absolute;
	left: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin posRightCenter() {
	position: absolute;
	right: 0;
	bottom: 0;
	top: 0;
	margin: auto; }

@mixin placeholder($color) {
	&::-webkit-input-placeholder {
		color: $color; }
	&:-moz-placeholder {
		color: $color; }
	&::-moz-placeholder {
		color: $color; }
	&:-ms-input-placeholder {
		color: $color; } }

@mixin font($font_name, $file__name, $weight, $style) {
	@font-face {
		font-family: $font_name;
		font-display: swap;
		src: url('../fonts/' + $file__name + '.woff') format('woff'), url('../fonts/' + $file__name + '.woff2') format('woff2');
		font-weight: $weight;
		font-style: $style; } }

@import "fonts";
@import "reset";
@import "sumoselect";
@import "jquery.mCustomScrollbar";
@import "styles";
@import "form";
@import "rangeslider";
@import "../components/header/header";
@import "../components/top-info/top-info";
@import "../components/ordering/ordering";
@import "../components/footer/footer";
@import "../components/menu/menu";
