.footer {
	padding-top: 15px;
	margin-top: auto;
	background-color: #3D4050;
	color: #fff;
	font-size: 13px; }

.footer__copyright {
	margin-bottom: 15px;
	text-align: center; }

.footer__payments {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center; }

.footer__payments-item {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 10px;
	margin-bottom: 15px;
	color: #C3C3C3; }

.footer__payments-icon {
	margin-right: 10px;
	flex-shrink: 1; }

.footer__contacts {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center; }

.footer__contacts-item {
	padding: 0 10px;
	margin-bottom: 15px; }

.footer__contacts-link {
	display: flex;
	align-items: center;
	color: #fff;
	text-decoration: none; }

.footer__contacts-icon {
	margin-right: 10px;
	flex-shrink: 1; }

@media only screen and (min-width: 768px) {
	.footer__row {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin: 0 -10px; }
	.footer__copyright {
		position: relative;
		width: 50%;
		padding: 0 10px;
		&:after {
			@include posRightCenter;
			content: '';
			height: 18px;
			border-left: 1px solid #C3C3C3; } }
	.footer__payments {
		width: 50%; }
	.footer__contacts {
		width: 100%; } }

@media only screen and (min-width: 1200px) {
	.footer {
		padding: 30px 0 15px; }
	.footer__row {
		align-items: flex-start;
		justify-content: space-between;
		margin: 0; }
	.footer__copyright {
		width: 30%;
		text-align: left;
		padding: 3px 0 0 0;
		&:after {
			display: none; } }
	.footer__payments {
		width: 40%;
		position: relative;
		&:before, &:after {
			position: absolute;
			top: 0;
			content: '';
			height: 18px;
			border-left: 1px solid #C3C3C3; }
		&:before {
			left: 0; }
		&:after {
			right: 0; } }
	.footer__payments-item {
		padding: 0 19px; }
	.footer__contacts {
		width: 30%; }
	.footer__contacts-item {
		justify-content: flex-start;
		padding: 0 0 0 38px; } }

@media (hover), (min-width: 0\0), (min--moz-device-pixel-ratio: 0) {
	.footer__contacts-link {
		&:hover {
			text-decoration: underline; } } }
