.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 20;
	background-color: rgba(16, 16, 29, 0.9);
	padding: 18px 0; }

.header__container {
	display: flex;
	justify-content: space-between;
	align-items: center; }

.header__logo {
	display: flex;
	max-width: 180px; }

@media only screen and (min-width: 1200px) {
	.header {
		padding: 39.5px 0; }
	.header__logo {
		max-width: 245px; } }
