.main {
	overflow: hidden;
	display: flex;
	flex-direction: column;
	min-height: 100vh; }

.main__container {
	max-width: 1200px;
	width: 100%;
	margin: 0 auto;
	padding: 0 15px; }

.main__row {
	margin: 0 -15px; }

.clearfix {
	*zoom: 1;
	&:before,
	&:after {
		display: table;
		content: "";
		line-height: 0; }
	&:after {
		clear: both; } }

.flex-wrp {
	display: flex;
	flex-wrap: wrap; }

.object-fit {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	object-fit: cover; }

.icon {
	fill: $base; }
