.top-info {
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding: 100px 0 40px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	h1, .h1 {
		max-width: 300px;
		margin-bottom: 20px;
		text-transform: uppercase;
		font-size: 18px;
		font-weight: 700;
		line-height: 1.3;
		span {
			color: $blue; } }
	p {
		margin-bottom: 10px; } }

.top-info__body {
	> *:last-child {
		margin-bottom: 0; } }

.top-info__text {
	max-width: 543px;
	margin-bottom: 20px;
	font-size: 16px;
	line-height: 1.2;
	> *:last-child {
		margin-bottom: 0; } }

.top-info__ul {
	margin-bottom: 10px;
	li {
		padding-left: 25px;
		margin-bottom: 5px;
		position: relative;
		&:last-child {
			margin-bottom: 0; }
		&:before {
			content: '';
			position: absolute;
			left: 10px;
			top: 7px;
			width: 5px;
			height: 2px;
			background-color: $base; } } }

.top-info__btn-wrapper {
	display: flex;
	flex-wrap: wrap;
	.btn {
		margin-right: 10px; } }

@media only screen and (min-width: 768px) {
	.top-info {
		min-height: 600px;
		.h1, h1 {
			max-width: 400px;
			margin-bottom: 40px;
			font-size: 22px; } }
	.top-info__text {
		margin-bottom: 40px; }
	.top-info__btn-wrapper {
		.btn {
			min-width: 183px;
			margin-right: 30px; } } }

@media only screen and (min-width: 1200px) {
	.top-info {
		padding: 150px 0;
		.h1, h1 {
			max-width: 780px;
			margin-bottom: 80px;
			font-size: 48px; } }
	.top-info__text {
		max-width: 780px;
		margin-bottom: 80px;
		font-size: 20px; }
	.top-info__ul {
		li:before {
			top: 10px; } } }

@media only screen and (min-width: 1600px) {
	.top-info {
		min-height: 850px; } }
