.ordering {
	padding: 40px 0 10px;
	background: #272733 center / cover no-repeat url('../images/img1920x920.jpg'); }

.ordering__top {
	margin-bottom: 30px;
	text-align: center;
	h2, .h2 {
		margin-bottom: 20px;
		color: #fff;
		font-weight: 700;
		font-size: 22px;
		text-transform: uppercase;
		span {
			color: $blue; } }
	p {
		margin-bottom: 10px;
		color: #bbb;
		font-size: 16px; }
	> *:last-child {
		margin-bottom: 0; } }

.ordering__steps {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin: 0 -15px;
	color: #fff;
	font-size: 16px;
	line-height: 1.2; }

.ordering__steps-col {
	position: relative;
	width: 100%;
	padding: 0 15px 60px;
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	&:before {
		content: '';
		position: absolute;
		margin: auto;
		background: url('../images/dots.svg') repeat-x center; } }

.ordering__steps-icon {
	display: block;
	width: 80px;
	height: 80px;
	position: relative;
	background: #3E9CDC;
	border: 5px solid #286690;
	border-radius: 50%;
	margin-bottom: 20px;
	img {
		@include posCenter; } }

.ordering__form-row {
	@extend .flex-wrp;
	justify-content: center;
	margin: 0 -15px; }

.ordering__form-col {
	flex-grow: 1;
	width: 100%;
	padding: 0 15px;
	margin-bottom: 30px; }

.ordering__form-col_btn {
	flex-grow: 0; }

.ordering_btn {
	width: 100%;
	padding: 17px 30px 16px; }

.ordering__range-top {
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
	margin-bottom: 20px;
	color: #fff; }

.ordering__range-value {
	flex-shrink: 0;
	font-weight: 600; }

.range-slider {
	height: 7px;
	opacity: 0; }

@media only screen and (max-width: 639px) {
	.ordering__steps-col {
		padding-bottom: 95px;
		&:before {
			content: '';
			position: absolute;
			bottom: 50px;
			left: 0;
			right: 0;
			margin: auto;
			width: 60px;
			height: 6px;
			transform: rotate(90deg); }
		&:last-child {
			padding-bottom: 30px;
			&:before {
				display: none; } } } }

@media only screen and (min-width: 640px) and (max-width: 991px) {
	.ordering__steps-col {
		&:last-child {
			&:before {
				display: none; } }
		&:nth-child(3n) {
			&:before {
				display: none; } } } }

@media only screen and (min-width: 640px) {
	.ordering__steps-col {
		width: 33.33333333%;
		&:before {
			width: 84px;
			height: 6px;
			top: 38px;
			right: -42px;
			background-position: left center; }
		&:last-child {
			&:before {
				display: none; } } }
	.ordering__form-col {
		width: 50%; } }


@media only screen and (min-width: 768px) {
	.ordering {
		padding: 80px 0 50px; } }

@media only screen and (min-width: 992px) {
	.ordering__steps {
		margin-bottom: 30px; }
	.ordering__steps-col {
		width: 20%;
		padding-bottom: 30px;
		&:before {
			top: 50px; }
		&:nth-child(5n) {
			&:before {
				display: none; } } }
	.ordering__steps-icon {
		width: 100px;
		height: 100px; }
	.ordering__range-top {
		font-size: 18px; }
	.ordering__form-col {
		width: 33.33333333%; }
	.ordering__form-col_2 {
		width: 66.66666667%; }
	.ordering__form-col_btn {
		padding-top: 25px; }
	.ordering__top p {
		font-size: 18px; } }

@media only screen and (min-width: 1200px) {
	.ordering__steps {
		margin-bottom: 50px; }
	.ordering__steps-col {
		&:before {
			width: 110px;
			right: -55px; } }
	.ordering {
		padding: 120px 0 120px; }
	.ordering__top {
		margin-bottom: 80px;
		.h2, h2 {
			margin-bottom: 30px;
			font-size: 36px; } } }
